'use client'
import { useRef, useState } from "react";

export default function CasinoApiPage() {

const [message, setMessage] = useState("");

const name = useRef<HTMLInputElement>(null);
const weight = useRef<HTMLInputElement>(null);

const submit = async (e: React.FormEvent) => {

    const label = name.current?.value;
    const w = weight.current?.value;

    if(!label || !w) return;

    try {
        const res = await fetch("api/lots", {
            method: "POST",
            headers: {"Content-Type": "application/json"},
            body: JSON.stringify({
                label,
                weight: Number(w),
                createdAt: Date.now()
            })
        });

        if (res.ok){
            setMessage('ok');
        } else{
            const error = await res.json()
            setMessage(error.error);
        }
    } catch (err) {
        setMessage('ошибка сети')
    } 

    

    name.current!.value = "";
    weight.current!.value = "";
}


    return (
        <div className="flex justify-center items-center h-screen ">
            <form 
                onSubmit={(e) => {
                    e.preventDefault();
                    submit(e)
                }}
                className="flex flex-col items-center justify-center p-2 gap-3">

                <input ref={name} className="p-2 border rounded-md" placeholder="name lot" type="text" />

                <input ref={weight} className="p-2 border rounded-md" placeholder="weight lot" type="number" />

                <button type="submit" className="p-2 bg-blue-500 text-white rounded-md">Submit</button>
            </form>

            <div>
                {message}
            </div>
        </div>
    )
}